import { Component, Input } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { MoneyService } from '../../../shared/money.service';

/**
 * Generated class for the MoneyInfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-money-info',
  templateUrl: 'money-info.html',
})
export class MoneyInfoPage {

  private userMoney;
  private user;
  
  constructor(public navCtrl: NavController, public navParams: NavParams, private _moneyService: MoneyService) {
  }

  ngOnInit() {
    this.user = JSON.parse(window.localStorage.getItem('user'));
    // console.log(window.localStorage);
    this._moneyService.getMoneyInfo(this.user.userID).subscribe((response: any) => {
      this.userMoney = response;
      console.log(this.userMoney);
    }, error => {
      console.log('Error from api!');
    });
  }
}



